.filler{
    display: none;
}
.projects{
    height: 100vh;
    overflow-y: scroll;
}

.picture-container{
    width: 65vw;
}
.modal-pictures-sub{
    grid-auto-rows: 6%;
    position: relative;
}
.photoCollection{
    left: 0;
    
} 
.show{
    height: 500vh;
}
}

